import { useState } from "react";
import "./App.css";
import useForm from "./hooks/useForm";

function App() {
  const [formValues, handleInputChange, reset] = useForm({
    url: "",
  });
  const [tokens, setTokens] = useState([]);
  const [load, setLoad] = useState(false);
  const { url } = formValues;

  const handleSubmit = async (e) => {
    setLoad(true);
    const response = await fetch(`https://npl-back.leonelparrales.com//tokens?url=${url}`);
    const data = await response.json();
    setLoad(false);
    setTokens(data.tokens);
  };

  return (
    <div className="container">
      <h1 className="mt-4">NPL Aplication</h1>
      <p>Ingrese la URL a tokenizar:</p>
      <input className="form-control form-control-lg" type="text" placeholder="Ingrese URL" aria-label=".form-control-lg example" autoComplete="off" name="url" value={url} onChange={handleInputChange} disabled={load} />

      <div className="d-grid gap-2 mt-2">
        <button className="btn btn-primary" type="button" onClick={handleSubmit} disabled={load}>
          Tokenizar
        </button>
      </div>

      <hr />
      <h2 className="mt-4">Resultado: </h2>

      <p>
        {load ? (
          <div class="spinner-border text-primary" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
        ) : (
          <div>
            {tokens.map((element, i) => {
              return <li key={i}>{element}</li>;
            })}
          </div>
        )}
      </p>
    </div>
  );
}

export default App;
